import { connect } from 'react-redux';
import { fileName,real_filename, schema, relations, storyParameter, maxStoryLength, data, resultCoverage, aggregationLevel, facts, rewardWeight, title, generateProgress, hoverFactIndex ,filters,filtersTitle,editstate,editingCurrentFact,schemaFilterLocal,dataresLocal,unusedFacts,filtersLocal,datares,schemaFilter,filtersChangeFlag,filtersTitleLocal,method} from '@/selector/story';
import { cardItems,videoIndex,leftContent,barWidthList,isPlayVideo,narrative_spec,question,temp_spec,operatorType,animationType,keys,isGenerating} from '@/selector/video';
import * as dataAction from '@/action/dataAction';
import * as factAction from '@/action/factAction';
import * as storyAction from '@/action/storyAction';
import * as videoAction from '@/action/videoAction';
import LeftSidebar from './LeftSidebar.js';

const mapStateToProps = (state) => ({
    schema: schema(state),
    facts: facts(state),
    data: data(state),
    relations: relations(state),
    title: title(state),
    resultCoverage: resultCoverage(state),
    fileName: fileName(state),
    real_filename: real_filename(state),
    storyParameter: storyParameter(state),
    maxStoryLength: maxStoryLength(state),
    rewardWeight: rewardWeight(state),
    aggregationLevel: aggregationLevel(state),
    generateProgress: generateProgress(state),
    filters:filters(state),
    filtersTitle:filtersTitle(state),
    hoverFactIndex: hoverFactIndex(state),
    //
    editstate:editstate(state),
    editingCurrentFact:editingCurrentFact(state),
    schemaFilterLocal:schemaFilterLocal(state) ,
    dataresLocal:dataresLocal(state),
    unusedFacts: unusedFacts(state),
    filtersLocal:filtersLocal(state),
    datares:datares(state),
    schemaFilter:schemaFilter(state),
    filtersChangeFlag:filtersChangeFlag(state),
    filtersTitleLocal:filtersTitleLocal(state),

    cardItems: cardItems(state),
    videoIndex:videoIndex(state),
    leftContent:leftContent(state),
    barWidthList:barWidthList(state),
    method: method(state),
    isPlayVideo:isPlayVideo(state),

    narrative_spec : narrative_spec(state),
    question : question(state),
    temp_spec : temp_spec(state),
    operatorType:operatorType(state),
    animationType:animationType(state),
    keys:keys(state),
    isGenerating:isGenerating(state),
})

const mapDispatchToProps = dispatch => {
    return {
        addCard:(fact,specs,time,insert,isGeneratedByAlg)=>dispatch(videoAction.addCard(fact,specs,time,insert,isGeneratedByAlg)),
        addEmptyCard:()=>dispatch(videoAction.addEmptyCard()),

        visualizeData: (data) => dispatch(dataAction.visualizeData(data)),
        changeData: (fileName,real_filename) => dispatch(dataAction.changeData(fileName,real_filename)),
        uploadData: (fileName, real_filename, schema, data) => dispatch(dataAction.uploadData(fileName,real_filename, schema, data)),
        //fact
        addFact: (facts) => dispatch(factAction.addFact(facts)),
        updateFact: (index, fact) => dispatch(factAction.updateFact(index, fact)),
        deleteFact: (index) => dispatch(factAction.deleteFact(index)),
        orderFacts: (sourceIndex, destinationIndex) => dispatch(factAction.orderFacts(sourceIndex, destinationIndex)),
        //story
        selectFact: (index) => dispatch(storyAction.selectFact(index)),
        generateStory: (facts, relations, coverage) => dispatch(storyAction.generateStory(facts, relations, coverage)),
        updateProgress: (totalLength, currentLength) => dispatch(storyAction.updateProgress(totalLength, currentLength)),
        changeMethod: (method) => dispatch(storyAction.changeMethod(method)),
        setStoryParameter: (maxStoryLength, information, chartDiversity, timeLimit) => dispatch(storyAction.setStoryParameter(maxStoryLength, information, chartDiversity, timeLimit)),
        setRewardWeight: (logicality, diversity, integrity) => dispatch(storyAction.setRewardWeight(logicality, diversity, integrity)),
        setAggregationLevel: (level) => dispatch(storyAction.setAggregationLevel(level)),
        changeTitle: (title) => dispatch(storyAction.changeTitle(title)),

        //filters
        updateFilters:(filters)=>dispatch(factAction.updateFilters(filters)),
        updateFiltersTitle:(filtersTitle)=>dispatch(factAction.updateFiltersTitle(filtersTitle)),
        updateFiltersChangeFlag:(filtersChangeFlag)=>dispatch(factAction.updateFiltersChangeFlag(filtersChangeFlag)),
        
        //edit filters
        updateFiltersLocal: (filtersLocal, filtersTitleLocal, dataresLocal, schemaFilterLocal) => dispatch(factAction.updateFiltersLocal(filtersLocal, filtersTitleLocal, dataresLocal, schemaFilterLocal)), 
       
        deleteUnusedFact: (index) => dispatch(factAction.deleteUnusedFact(index)),
        initialVideoSlice: (facts,schemaFilter,filename,datares,schema) => dispatch(videoAction.initialVideoSlice(facts,schemaFilter,filename,datares,schema)),
        generateStory: (facts, relations, coverage) => dispatch(storyAction.generateStory(facts, relations, coverage)),
        addBarToTimeLineBar:(bar,index)=>dispatch(videoAction.addBarToTimeLineBar(bar,index)),
        setLeftsiderContent:(content)=>dispatch(videoAction.setLeftsiderContent(content)),
        deleteBarFromTimeLineBar:(index)=>dispatch(videoAction.deleteBarFromTimeLineBar(index)),
        initTimeLineBarWidth:(length)=>dispatch(videoAction.initTimeLineBarWidth(length)),
        setPosition: (position) => dispatch(videoAction.setPosition(position)),
        changeCurrentEditing:(editstate,editingCurrentFact)=>dispatch(factAction.changeCurrentEditing(editstate,editingCurrentFact)),
        playVideoSlice:(index)=>dispatch(videoAction.playVideoSlice(index)),
        exchangeCard:(index,card)=>dispatch(videoAction.exchangeCard(index,card)),
        insertFact: (index, insert) => dispatch(factAction.insertFact(index, insert)),
        
        question2Gif : (question, narrative_spec) => dispatch(videoAction.question2Gif(question, narrative_spec)),
        changeTempSpec : (temp_spec) => dispatch(videoAction.changeTempSpec(temp_spec)),
        isGeneratingGIF : (isGenerating) => dispatch(videoAction.isGeneratingGIF(isGenerating)),
        operatorChange : (operatorType) => dispatch(videoAction.operatorChange(operatorType)),
        animationChange : (animationType) => dispatch(videoAction.animationChange(animationType)),
        changeKeys : (keys) => dispatch(videoAction.changeKeys(keys)),
        playVideoContinue:()=>dispatch(videoAction.playVideoContinue()),
        stopPlayVideoContinue:()=>dispatch(videoAction.stopPlayVideoContinue()),
        changeAllVideoTime:(time)=>dispatch(videoAction.changeAllVideoTime(time)),
        changeNowVideoTime:(time)=>dispatch(videoAction.changeNowVideoTime(time)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LeftSidebar);
